import React, {Component} from 'react';
import {Card, Select, Button, Input, Table, message} from 'antd';
import {PlusOutlined} from "@ant-design/icons";

export default class PetHome extends Component {

    state = {
        pets: [
            {
                "name": "",
                "level": 85,
                "atk": 928,
                "mg_atk": 454,
            },
            {
                "name": "",
                "level": 85,
                "atk": 262,
                "mg_atk": 622,
            }
        ]
    }

    initColumns = () => {
        // 
        this.columns = [
            {
                title: "",
                dataIndex: "name",
                key: "name"
            },
            {
                title: "",
                dataIndex: "level",
                key: "level",
                render: (level) => 'Lv ' + level
            },
            {
                title: "",
                dataIndex: "atk",
                key: "atk"
            },
            {
                title: "",
                dataIndex: "mg_atk",
                key: "mg_atk"
            }
        ];
    }

    showModal = () => {
        message.info("");
    }

    componentWillMount() {
        this.initColumns();
    }

    render() {
        const {pets} = this.state;
        const title = (
            <span>
                <Select value='1'>
                    <Select.Option value="1"></Select.Option>
                    <Select.Option value="2"></Select.Option>
                </Select>
                <Input placeholder="" style={{width: "150px", margin: "0 5px"}}/>
                <Button type="primary"></Button>
            </span>
        );
        const extra = (
            <Button type="primary" onClick={() => this.showModal()}>
                <PlusOutlined/>
                
            </Button>
        );
        return (
            <Card title={title} extra={extra}>
                <Table
                    dataSource={pets}
                    columns={this.columns}
                    rowKey="key"
                    bordered={true}/>
            </Card>
        );
    }

}